
import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Search } from './Search';
import { Episodes } from './Episodes';

function MyApp() {
  const [selectedMovieId, setSelectedMovieId] = useState(null);

  // Callback function to set the selected movie ID
  const handleMovieClick = (movieId) => {
    setSelectedMovieId(movieId);
  };

  return (
    <Router>
      <div>
        <Routes>
          {/* Update the route path to match the structure */}
          <Route path="episodes/:movieId" element={<Episodes />} />
          {/* Render the Search component and pass the setId callback */}
          <Route path="*" element={<Search onMovieSelect={handleMovieClick} />} />
        </Routes>
      </div>
    </Router>
  );
}

export default MyApp;