import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Episodes from './Episodes';


export function Search({ onMovieSelect }) {
  const [query, setQuery] = useState('');
  const [results, setResults] = useState([]);
  const [selectedMovieId, setSelectedMovieId] = useState(null);
  const [showSearchResults, setShowSearchResults] = useState(true);
  const navigate = useNavigate();

  const searchMovies = () => {
    if (query.trim() !== '') {
      setResults([]);

      const apiUrl = `https://api-shiori.vercel.app/movies/flixhq/${query}?page=1`;

      fetch(apiUrl)
        .then((response) => response.json())
        .then((data) => {
          setResults(data.results);
          setShowSearchResults(true);
          console.log(data.results);
        })
        .catch((error) => console.error('Error:', error));
    }
  };

  useEffect(() => {
    if (selectedMovieId) {
      setShowSearchResults(false);
    }
  }, [selectedMovieId]);

  const handleMovieClick = (movieId) => {
    console.log(movieId);
    navigate(`/${movieId}`);
    setSelectedMovieId(movieId);
  };

  return (
    <div>
      <h1>Movie Search</h1>
      <input
        type="text"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        placeholder="Enter a movie title"
      />
      <button onClick={searchMovies}>Search</button>
      <div className="grid-container">
        {showSearchResults && results.length > 0 ? (
          results.map((movie) => (
            <div key={movie.id} className="movie-card">
              <img
                src={movie.image}
                alt={movie.title}
                className="movie-image"
              />
              <div className="movie-info">
                <strong>{movie.title}</strong>
                <br />
                <button className="play-now-button" onClick={() => handleMovieClick(movie.id)}>
                  Play Now
                </button>
              </div>
            </div>
          ))
        ) : selectedMovieId ? (
          <div>
            <Episodes selectedMovieId={selectedMovieId} />
          </div>
        ) : (
          <p>No results found.
            <br />
            Just because I use 'Lucida Console' font with a green color with a black background does not prove I'm a 'hacker'.
          </p>
        )}
      </div>
    </div>
  );
}

export default Search;